/**
 * @file Bundle Analyzer
 * @description ÂâçÁ´ØBundleÂàÜÊûêÂô®ÔºåÁõëÊéßÂíå‰ºòÂåñËµÑÊ∫êÂç†Áî®
 * @author ZK-Agent Team A
 * @date 2024-12-19
 */

// BundleÂàÜÊûêÁªìÊûúÁ±ªÂûã
interface BundleAnalysis {
  totalSize: number
  gzippedSize: number
  chunks: ChunkInfo[]
  assets: AssetInfo[]
  dependencies: DependencyInfo[]
  recommendations: Recommendation[]
  performance: PerformanceMetrics
}

interface ChunkInfo {
  name: string
  size: number
  gzippedSize: number
  modules: string[]
  isEntry: boolean
  isAsync: boolean
}

interface AssetInfo {
  name: string
  size: number
  type: 'js' | 'css' | 'image' | 'font' | 'other'
  compressed: boolean
  cacheable: boolean
}

interface DependencyInfo {
  name: string
  version: string
  size: number
  usage: 'full' | 'partial' | 'unused'
  treeshakeable: boolean
  alternatives?: string[]
}

interface Recommendation {
  type: 'size' | 'performance' | 'dependency' | 'caching'
  severity: 'low' | 'medium' | 'high' | 'critical'
  title: string
  description: string
  impact: string
  solution: string
  estimatedSavings?: number
}

interface PerformanceMetrics {
  firstContentfulPaint: number
  largestContentfulPaint: number
  cumulativeLayoutShift: number
  firstInputDelay: number
  timeToInteractive: number
  totalBlockingTime: number
}

// BundleÂàÜÊûêÂô®Á±ª
export class BundleAnalyzer {
  private analysis: BundleAnalysis | null = null
  private thresholds = {
    maxBundleSize: 500 * 1024, // 500KB
    maxChunkSize: 250 * 1024,  // 250KB
    maxAssetSize: 100 * 1024,  // 100KB
    minCompressionRatio: 0.7,  // 70%ÂéãÁº©Áéá
    maxDependencies: 50,       // ÊúÄÂ§ß‰æùËµñÊï∞
    maxUnusedCode: 0.1         // 10%Êú™‰ΩøÁî®‰ª£Á†Å
  }

  /**
   * ÂàÜÊûêBundle
   */
  async analyzeBundleSize(): Promise<BundleAnalysis> {
    try {
      // Ê®°ÊãüBundleÂàÜÊûêÊï∞ÊçÆ
      const mockAnalysis: BundleAnalysis = {
        totalSize: 485 * 1024, // 485KB
        gzippedSize: 145 * 1024, // 145KB
        chunks: [
          {
            name: 'main',
            size: 280 * 1024,
            gzippedSize: 85 * 1024,
            modules: ['react', 'react-dom', 'next', 'app'],
            isEntry: true,
            isAsync: false
          },
          {
            name: 'vendor',
            size: 150 * 1024,
            gzippedSize: 45 * 1024,
            modules: ['lodash', 'moment', 'axios'],
            isEntry: false,
            isAsync: false
          },
          {
            name: 'components',
            size: 55 * 1024,
            gzippedSize: 15 * 1024,
            modules: ['components/ui', 'components/welcome'],
            isEntry: false,
            isAsync: true
          }
        ],
        assets: [
          {
            name: 'main.js',
            size: 280 * 1024,
            type: 'js',
            compressed: true,
            cacheable: true
          },
          {
            name: 'vendor.js',
            size: 150 * 1024,
            type: 'js',
            compressed: true,
            cacheable: true
          },
          {
            name: 'styles.css',
            size: 35 * 1024,
            type: 'css',
            compressed: true,
            cacheable: true
          },
          {
            name: 'logo.svg',
            size: 8 * 1024,
            type: 'image',
            compressed: false,
            cacheable: true
          }
        ],
        dependencies: [
          {
            name: 'react',
            version: '18.2.0',
            size: 45 * 1024,
            usage: 'full',
            treeshakeable: false
          },
          {
            name: 'lodash',
            version: '4.17.21',
            size: 70 * 1024,
            usage: 'partial',
            treeshakeable: true,
            alternatives: ['lodash-es', 'ramda']
          },
          {
            name: 'moment',
            version: '2.29.4',
            size: 67 * 1024,
            usage: 'partial',
            treeshakeable: false,
            alternatives: ['date-fns', 'dayjs']
          }
        ],
        recommendations: [],
        performance: {
          firstContentfulPaint: 1200,
          largestContentfulPaint: 2100,
          cumulativeLayoutShift: 0.05,
          firstInputDelay: 45,
          timeToInteractive: 2800,
          totalBlockingTime: 150
        }
      }

      // ÁîüÊàê‰ºòÂåñÂª∫ËÆÆ
      mockAnalysis.recommendations = this.generateRecommendations(mockAnalysis)
      
      this.analysis = mockAnalysis
      return mockAnalysis
    } catch (error) {
      console.error('Bundle analysis failed:', error)
      throw new Error('BundleÂàÜÊûêÂ§±Ë¥•')
    }
  }

  /**
   * ÁîüÊàê‰ºòÂåñÂª∫ËÆÆ
   */
  private generateRecommendations(analysis: BundleAnalysis): Recommendation[] {
    const recommendations: Recommendation[] = []

    // Ê£ÄÊü•ÊÄªBundleÂ§ßÂ∞è
    if (analysis.totalSize > this.thresholds.maxBundleSize) {
      recommendations.push({
        type: 'size',
        severity: 'high',
        title: 'BundleÂ§ßÂ∞èË∂ÖÊ†á',
        description: `ÂΩìÂâçBundleÂ§ßÂ∞è‰∏∫${(analysis.totalSize / 1024).toFixed(1)}KBÔºåË∂ÖËøáÊé®ËçêÁöÑ${this.thresholds.maxBundleSize / 1024}KB`,
        impact: 'ÂΩ±ÂìçÈ¶ñÂ±èÂä†ËΩΩÊó∂Èó¥ÂíåÁî®Êà∑‰ΩìÈ™å',
        solution: 'ËÄÉËôë‰ª£Á†ÅÂàÜÂâ≤„ÄÅÊáíÂä†ËΩΩÂíå‰æùËµñ‰ºòÂåñ',
        estimatedSavings: analysis.totalSize - this.thresholds.maxBundleSize
      })
    }

    // Ê£ÄÊü•ÂéãÁº©Áéá
    const compressionRatio = analysis.gzippedSize / analysis.totalSize
    if (compressionRatio > this.thresholds.minCompressionRatio) {
      recommendations.push({
        type: 'performance',
        severity: 'medium',
        title: 'ÂéãÁº©ÊïàÊûú‰∏ç‰Ω≥',
        description: `ÂΩìÂâçÂéãÁº©Áéá‰∏∫${(compressionRatio * 100).toFixed(1)}%ÔºåÂèØ‰ª•Ëøõ‰∏ÄÊ≠•‰ºòÂåñ`,
        impact: 'Â¢ûÂä†‰º†ËæìÊó∂Èó¥ÂíåÂ∏¶ÂÆΩÊ∂àËÄó',
        solution: 'ÂêØÁî®BrotliÂéãÁº©„ÄÅ‰ºòÂåñ‰ª£Á†ÅÁªìÊûÑ',
        estimatedSavings: analysis.gzippedSize * 0.2
      })
    }

    // Ê£ÄÊü•Â§ßÂûã‰æùËµñ
    analysis.dependencies.forEach(dep => {
      if (dep.size > 50 * 1024 && dep.usage === 'partial') {
        recommendations.push({
          type: 'dependency',
          severity: 'medium',
          title: `${dep.name}‰æùËµñËøáÂ§ß`,
          description: `${dep.name}Âç†Áî®${(dep.size / 1024).toFixed(1)}KB‰ΩÜÂè™ÈÉ®ÂàÜ‰ΩøÁî®`,
          impact: 'Â¢ûÂä†‰∏çÂøÖË¶ÅÁöÑBundleÂ§ßÂ∞è',
          solution: dep.alternatives ? 
            `ËÄÉËôë‰ΩøÁî®Êõ¥ËΩªÈáèÁöÑÊõø‰ª£ÊñπÊ°àÔºö${dep.alternatives.join(', ')}` :
            'ËÄÉËôëÊåâÈúÄÂØºÂÖ•ÊàñÂØªÊâæÊõø‰ª£ÊñπÊ°à',
          estimatedSavings: dep.size * 0.6
        })
      }
    })

    // Ê£ÄÊü•ÊÄßËÉΩÊåáÊ†á
    if (analysis.performance.largestContentfulPaint > 2500) {
      recommendations.push({
        type: 'performance',
        severity: 'high',
        title: 'LCPÊÄßËÉΩ‰∏ç‰Ω≥',
        description: `ÊúÄÂ§ßÂÜÖÂÆπÁªòÂà∂Êó∂Èó¥‰∏∫${analysis.performance.largestContentfulPaint}msÔºåË∂ÖËøáÊé®ËçêÁöÑ2.5Áßí`,
        impact: 'ÂΩ±ÂìçÁî®Êà∑ÊÑüÁü•ÁöÑÂä†ËΩΩÈÄüÂ∫¶',
        solution: '‰ºòÂåñÂÖ≥ÈîÆËµÑÊ∫êÂä†ËΩΩ„ÄÅ‰ΩøÁî®È¢ÑÂä†ËΩΩ„ÄÅÂáèÂ∞ëÊ∏≤ÊüìÈòªÂ°û',
        estimatedSavings: 0
      })
    }

    // Ê£ÄÊü•ÁºìÂ≠òÁ≠ñÁï•
    const uncacheableAssets = analysis.assets.filter(asset => !asset.cacheable)
    if (uncacheableAssets.length > 0) {
      recommendations.push({
        type: 'caching',
        severity: 'medium',
        title: 'ÁºìÂ≠òÁ≠ñÁï•‰∏çÂÆåÂñÑ',
        description: `${uncacheableAssets.length}‰∏™ËµÑÊ∫êÊú™ÂêØÁî®ÁºìÂ≠ò`,
        impact: 'Â¢ûÂä†ÈáçÂ§çÂä†ËΩΩÊó∂Èó¥',
        solution: '‰∏∫ÈùôÊÄÅËµÑÊ∫êÈÖçÁΩÆÈÄÇÂΩìÁöÑÁºìÂ≠òÂ§¥',
        estimatedSavings: 0
      })
    }

    return recommendations.sort((a, b) => {
      const severityOrder = { critical: 4, high: 3, medium: 2, low: 1 }
      return severityOrder[b.severity] - severityOrder[a.severity]
    })
  }

  /**
   * Ëé∑ÂèñÊÄßËÉΩËØÑÂàÜ
   */
  getPerformanceScore(): number {
    if (!this.analysis) return 0

    const { performance } = this.analysis
    let score = 100

    // LCPËØÑÂàÜ (25%)
    if (performance.largestContentfulPaint > 4000) score -= 25
    else if (performance.largestContentfulPaint > 2500) score -= 15
    else if (performance.largestContentfulPaint > 1500) score -= 5

    // FIDËØÑÂàÜ (25%)
    if (performance.firstInputDelay > 300) score -= 25
    else if (performance.firstInputDelay > 100) score -= 15
    else if (performance.firstInputDelay > 50) score -= 5

    // CLSËØÑÂàÜ (25%)
    if (performance.cumulativeLayoutShift > 0.25) score -= 25
    else if (performance.cumulativeLayoutShift > 0.1) score -= 15
    else if (performance.cumulativeLayoutShift > 0.05) score -= 5

    // BundleÂ§ßÂ∞èËØÑÂàÜ (25%)
    if (this.analysis.totalSize > 1000 * 1024) score -= 25
    else if (this.analysis.totalSize > 500 * 1024) score -= 15
    else if (this.analysis.totalSize > 250 * 1024) score -= 5

    return Math.max(0, Math.min(100, score))
  }

  /**
   * ÁîüÊàê‰ºòÂåñÊä•Âëä
   */
  generateOptimizationReport(): string {
    if (!this.analysis) return 'ËØ∑ÂÖàËøêË°åBundleÂàÜÊûê'

    const score = this.getPerformanceScore()
    const { totalSize, gzippedSize, recommendations } = this.analysis

    let report = `
# Bundle‰ºòÂåñÊä•Âëä

## ÊÄª‰ΩìËØÑÂàÜ: ${score}/100

## Bundle‰ø°ÊÅØ
- ÊÄªÂ§ßÂ∞è: ${(totalSize / 1024).toFixed(1)}KB
- ÂéãÁº©Âêé: ${(gzippedSize / 1024).toFixed(1)}KB
- ÂéãÁº©Áéá: ${((1 - gzippedSize / totalSize) * 100).toFixed(1)}%

## ‰ºòÂåñÂª∫ËÆÆ (${recommendations.length}È°π)
`

    recommendations.forEach((rec, index) => {
      const severityEmoji = {
        critical: 'üî¥',
        high: 'üü†',
        medium: 'üü°',
        low: 'üü¢'
      }

      report += `
### ${index + 1}. ${severityEmoji[rec.severity]} ${rec.title}
- **ÈóÆÈ¢ò**: ${rec.description}
- **ÂΩ±Âìç**: ${rec.impact}
- **Ëß£ÂÜ≥ÊñπÊ°à**: ${rec.solution}
${rec.estimatedSavings ? `- **È¢ÑËÆ°ËäÇÁúÅ**: ${(rec.estimatedSavings / 1024).toFixed(1)}KB` : ''}
`
    })

    return report
  }

  /**
   * ÁõëÊéßBundleÂèòÂåñ
   */
  async monitorBundleChanges(): Promise<void> {
    // Ê®°ÊãüÁõëÊéßÈÄªËæë
    setInterval(async () => {
      try {
        const newAnalysis = await this.analyzeBundleSize()
        
        if (this.analysis) {
          const sizeDiff = newAnalysis.totalSize - this.analysis.totalSize
          
          if (Math.abs(sizeDiff) > 10 * 1024) { // 10KBÂèòÂåñ
            console.log(`BundleÂ§ßÂ∞èÂèòÂåñ: ${sizeDiff > 0 ? '+' : ''}${(sizeDiff / 1024).toFixed(1)}KB`)
            
            // Ëß¶ÂèëÂëäË≠¶
            if (sizeDiff > 50 * 1024) { // Â¢ûÂä†Ë∂ÖËøá50KB
              console.warn('‚ö†Ô∏è BundleÂ§ßÂ∞èÊòæËëóÂ¢ûÂä†ÔºåËØ∑Ê£ÄÊü•ÊúÄËøëÁöÑ‰ª£Á†ÅÂèòÊõ¥')
            }
          }
        }
        
        this.analysis = newAnalysis
      } catch (error) {
        console.error('BundleÁõëÊéßÂ§±Ë¥•:', error)
      }
    }, 30000) // 30ÁßíÊ£ÄÊü•‰∏ÄÊ¨°
  }

  /**
   * Ëé∑ÂèñÂΩìÂâçÂàÜÊûêÁªìÊûú
   */
  getCurrentAnalysis(): BundleAnalysis | null {
    return this.analysis
  }
}

// ÂàõÂª∫ÂÖ®Â±ÄÂÆû‰æã
export const bundleAnalyzer = new BundleAnalyzer()

// Ëá™Âä®ÂêØÂä®ÁõëÊéßÔºà‰ªÖÂú®ÂºÄÂèëÁéØÂ¢ÉÔºâ
if (typeof window === 'undefined' && typeof process !== 'undefined') {
  try {
    // @ts-ignore - process.envÂú®Node.jsÁéØÂ¢É‰∏≠ÂèØÁî®
    if (process.env?.NODE_ENV === 'development') {
      bundleAnalyzer.monitorBundleChanges()
    }
  } catch (error) {
    // ÂøΩÁï•ÁéØÂ¢ÉÊ£ÄÊü•ÈîôËØØ
  }
} 